/* Подключение шрифтов */
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@500;700;900&display=swap');
// Локальный шрифт
//@use 'fonts';

/* Шрифт по умолчанию */
$mainFont: 'Mulish', sans-serif;
$fontSize: 1.6rem;
$lineHeight: 2.2rem;
$fontWeight: 400;

$secondFont: 'Manrope', sans-serif;
$thirdFont: 'Exo 2', sans-serif;

/* Основные цвета */
$mainColor: #333333; // Цвет шрифта по умолчанию

$backgroundMain: #ffffff; // Цвет фона body

$backgroundDark: #1E1E1E; // Цвет фона блоков
$backgroundLight: #C4C4C4;


/* Ограничивающий контейнер */
$maxWidth: 132em; // Ширина контейнера

/* Подключение обнуления */
@import './block/base';

body {
  font-family: $mainFont;
  font-size: $fontSize;
  line-height: $lineHeight;
  font-weight: $fontWeight;
  color: $mainColor;
  background-color: $backgroundMain;
}

img {
  width: 100%;
  //max-width: 100%;
  max-width: 2rem;
  object-fit: cover;
  display: block;
}
/* Стили оболочки */
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  // Прижимаю футер
  > main {
    flex: 1 1 auto;
  }
}
.container {
  outline: 1px solid red;
  max-width: $maxWidth;
  width: 100%;
  margin: 0 auto;
  padding: 0 2rem;
}
/* Подключение стилей общих элементов проекта */
@import './common.scss';

@import './chunks/header';
@import './chunks/calculate';
@import './chunks/footer';


/* Подключение отдельных блоков */
@import './block/burger';


