.calculator {

}
.calculator .container {
  padding: 0 10rem;
}

.calculator__inner {
  padding: 8rem 0;
}
.calculator__block {
  display: flex;
  gap: 3rem;
}
.calculator__selects {
  display: flex;
  gap: 2rem;
  flex: 1 1 60%;
  margin-bottom: 3rem;
}

.calculator__fields {
  padding: 5rem 6rem 3rem 6rem;
  flex: 1 1 40%;
  border-radius: 1rem;
  background-color: #7E7E7E;
}

.calculator__fields-title {
  color: #fff;
  text-align: center;
  font-family: $secondFont;
  font-weight: 700;
  font-size: 2.8rem;
  line-height: 3.8rem;
}
.calculator__fields-day-price {
  margin: 2rem 0;
  color: #fff;
  font-size: 2rem;
}

.calculator__fields-day-price--span {
  padding-left: 1rem;
  color: #014493;
  font-size: 3rem;
  font-weight: 700;
}

.calculator__fields-day-amount {
  color: #fff;
  font-size: 2rem;
  display: flex;
}

.calculator__fields-day-amount input {
  margin-left: 1rem;
  border-radius: 1rem;
  outline: none;
  border: none;
  padding: 0.5rem;
  width: 7rem; 
  color: #014493;
  font-size: 3rem;
  font-weight: 700;
}
.calculator__fields-guarantie {
  margin: 2rem 0;
  color: #fff;
  font-size: 2rem;
  display: flex;
}
.calculator__fields-guarantie--span {
  color: #014493;
  font-size: 3rem;
  font-weight: 700;
  padding-left: 1rem;
}
.calculator__button {
  height: 5rem;
  margin: 0 auto;
  display: block;
}
.calculator__fields-total {
  margin-top: 2rem;
  color: #fff;
  font-size: 2rem;
}
.calculator__fields-total--span {
  color: #014493;
  font-size: 3rem;
  font-weight: 700;
  padding-left: 1rem;
}



/* CUSTOM */
.select__classes {
  flex: 1 1 50%;
  position: relative;
}

.select__classes-button {
  width: 100%;
  position: relative;
  display: block;
  text-align-last: left;
  border-radius: 6px;
  background-color: #fff;
  border: 1px solid #7e9bbd;
  height: 46px;
  padding: 13px 20px;
  cursor: pointer;
}
.select__classes-button:focus, .select__classes-button--active {
  border: 1px solid red;
}

.select__classes-list {
  
  display: none;
  position: absolute;
  top: 5.2rem;
  left: 0;
  background-color: #7e9bbd;
  overflow: hidden;
  width: 20%;
  z-index: 1;
}
.select__classes-list--visible {
  display: block;
  width: 100%;
}
.select__classes-list-item {
  border: 1px solid #000;
  overflow: hidden;
  border-radius: 6px;
  border-bottom: 0;
  padding: 1.3rem 2rem;
  cursor: pointer;
}
.select__classes-list-item:first-child {
  border-radius: 6px 6px 0 0;
}
.select__classes-list-item:last-child {
  border-bottom: 1px solid #000;
  border-radius: 0 0 6px 6px;
}

.select__classes-list-item:hover {
  background-color: aliceblue;
}

.select__input-class {
  z-index: -1;
  position: absolute;
  visibility: hidden;
}

/* SELECT CARS */
.select__cars {
  flex: 1 1 50%;
  position: relative;
  display: none;
}
.select__cars--visible {
  display: block;
}
.select__cars-button {
