/* Общие стили элементов */

.inner {
  padding: 5.5rem 0;
}

.text__content {
  font-size: 1.8rem;
  line-height: 2.3rem;
}
.title {
  font-family: $thirdFont;
  display: inline-block;
  font-size: 3.4rem;
  line-height: 4.1rem;
  text-transform: uppercase;
  font-weight: 700;
}
.button {
  outline: none;
  border: none;
  cursor: pointer;
  border-radius: 1rem;
  padding: 0 5.9rem;
  font-weight: 500;
  text-transform: uppercase;
}